// import axios from "axios";

const state = {
    cart: []
};

const mutations = {
    set_cart: (state, product) => {
        //@TODO Ð¸Ð·Ð±Ð°Ð²Ð¸ÑÑÑ Ð¾Ñ Ð²Ð»Ð¾Ð¶ÐµÐ½Ð½Ð¾ÑÑÐ¸ if
        //@TODO Ð¸Ð·Ð±Ð°Ð²Ð¸ÑÑÑ Ð¾Ñ isPush, Ð¿ÑÐµÐ´Ð»Ð¾Ð´Ð¸ÑÑ Ð´ÑÑÐ³Ð¾Ð¹ Ð²Ð°ÑÐ¸Ð°Ð½Ñ
        if (state.cart.length) {
            let isPush = false;

            state.cart.map((item) => {
                if (item.id === product.id) {
                    isPush = true;
                    item.quantity++;
                }
            });

            if (!isPush) {
                product.quantity = 1;
                state.cart.push(product);
            }
        } else {
            product.quantity = 1;
            state.cart.push(product);
        }
    },
    remove_from_cart: (state, key) => {
        //@TODO Ð´Ð¾Ð±Ð°Ð²Ð¸ÑÑ Ð¿ÑÐ¾Ð²ÐµÑÐºÑ Ð½Ð° Ð½Ð°Ð»Ð¸ÑÐ¸Ðµ ÑÐ¾Ð²Ð°ÑÐ° Ð² ÐºÐ¾ÑÐ·Ð¸Ð½Ðµ
        //@TODO ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ðµ Ð½ÑÐ¶Ð½Ð¾ Ð´ÐµÐ»Ð°ÑÑ Ð¿Ð¾ id ÑÐ¾Ð²Ð°ÑÐ° Ð° Ð½Ðµ Ð¿Ð¾ Ð¿Ð¾ÑÑÐ´ÐºÐ¾Ð²Ð¾Ð¼Ñ Ð½Ð¾Ð¼ÐµÑÑ (ÐºÐ°Ðº Ð² favorites)

        state.cart.splice(key, 1)
    },
    decrement_quantity: (state, key) => {
        //@TODO Ð´Ð¾Ð±Ð°Ð²Ð¸ÑÑ Ð¿ÑÐ¾Ð²ÐµÑÐºÑ Ð½Ð° Ð½Ð°Ð»Ð¸ÑÐ¸Ðµ ÑÐ¾Ð²Ð°ÑÐ° Ð² ÐºÐ¾ÑÐ·Ð¸Ð½Ðµ
        if (state.cart[key].quantity <= 1) {
            state.cart.splice(key, 1)
        } else {
            state.cart[key].quantity--
        }
    },
    increment_quantity: (state, key) => {
        //@TODO Ð´Ð¾Ð±Ð°Ð²Ð¸ÑÑ Ð¿ÑÐ¾Ð²ÐµÑÐºÑ Ð½Ð° Ð½Ð°Ð»Ð¸ÑÐ¸Ðµ ÑÐ¾Ð²Ð°ÑÐ° Ð² ÐºÐ¾ÑÐ·Ð¸Ð½Ðµ
        state.cart[key].quantity++
    },
    clear_cart: (state) => {
        state.cart.length = 0
    }
};

const actions = {
    add_to_cart({commit}, product) {
        commit('set_cart', product);
    },
    remove_from_cart({commit}, key) {
        commit('remove_from_cart', key)
    },
    decrement_quantity({commit}, key) {
        commit('decrement_quantity', key)
    },
    increment_quantity({commit}, key) {
        commit('increment_quantity', key)
    },
    clear_cart({commit}) {
        commit('clear_cart')
    }
};

const getters = {
    CART(state) {
        return state.cart
    },
    get_total_price(state) {
        return state.cart.reduce((previous, current) => previous + (current.quantity * current.price), 0)
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}