import {createRouter, createWebHistory} from "vue-router";
import VCatalog from "../components/catalog/Catalog";
import HomePage from "../components/HomePage";
import Cart from "@/components/cart/Cart";

//@TODO Ð´Ð»Ñ ÑÐ°ÑÑÐ¾Ð² ÑÐµÐºÐ¾Ð¼ÐµÐ½Ð´ÑÐµÑÑÑ Ð¸ÑÐ¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÑ Ð´Ð¸Ð½Ð°Ð¼Ð¸ÑÐµÑÐºÐ¸Ð¹ Ð¸Ð¼Ð¿Ð¾ÑÑ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½ÑÐ¾Ð²

const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: "/",
            name: "catalog",
            component: VCatalog
        },
        {
            path: "/home-page",
            name: "home-page",
            component: HomePage
        },
        {
            path: "/cart",
            name: "cart",
            component: Cart
        }
    ]
});

export default router;